html, body {
    height: 100%;
}

body {
    margin: 0;
    padding: 0;
    width: 100%;
    display: table;
    font-weight: 100;
    font-family: 'Lato';
}

.container {
    text-align: center;
    display: table-cell;
    vertical-align: middle;
}

.content {
    text-align: center;
    display: inline-block;
}

.title {
    font-size: 30px;
}

a:-webkit-any-link {
    text-decoration: none;
    cursor: pointer;
}
a:hover{
    color: black;
}

.Alert
{
    padding: 10px;
}

.Alert--Success
{
    background: green;
}

.Alert--Error
{
    background: red;
}

.Alert--Info
{
    background: grey;
}